body {
  margin: 0;
  padding: 1rem;
  font-family: "Open Sans", -apple-system, BlinkMacSystemFont, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: white;
  max-width: 55rem;
  margin: 0 auto;
}

header h1 {
  display: none;
}

header img {
  width: 100%;
}

#video {
  width: 100%;
}

.fixed {
  position: fixed;
  top: 0;
}

h2 {
  padding: 1rem 0;
  width: 100%;
  background-color: black;
  z-index: 3;
  margin: 0;
}

a {
  /* text-decoration: none; */
  color: white;
}

a:focus {
  outline: none;
  border: 1px solid #b80000;
}

.more-info {
  position: absolute;
  /* left: 1em; */
  top: 0.25em;
  /* color: white; */
  font-size: 1.1em;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: flex-end;
  font-family: "Open Sans Condensed", -apple-system, BlinkMacSystemFont, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  z-index: 1;
  padding-right: 0.75rem;
  padding-top: 0.25rem;
}

.more-info * {
  float: left;
}

/* .info-icon {
  color: white;
}

.info-icon:hover {
  color: #b80000;
} */

.loading-shows {
  /* width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; */
  font-size: 1.5rem;
}

.shows {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: 1fr;
  margin: 0 -0.5rem;
}

.show {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  margin: 0.5rem;
  border: solid 0.5px #b80000;
}

.show:focus, .show:hover {
  outline: none;
  border: 0.5px solid white;
}

.show .svg {
  width: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

/* San Jose */

.svg.san-jose-12 {
  width: 75%;
}

/* Los Angeles - Oct 3 */

.svg.inglewood-17 {
  width: 90%;
}

/* Phoenix  */

.svg.glendale-15 {
  width: 110%;
}

/* Nashville */

.svg.nashville-2 {
  width: 83%;
}

.overview {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.loading {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: black;
}

.loading .text {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.loading img {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.info {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1.0rem;
  padding-top: 1.5rem;
  color: white;
}

.info div {
  margin-bottom: 0.5rem;
}

.infoBg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0.28;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.date {
  color: #b80000;
  font-size: 1.7rem;
}

.city {
  font-size: 1.29rem;
}

.venue:hover {
  text-decoration: underline;
}

.specialGuest {
  font-size: 0.85rem;
}

.tickets a {
  font-size: 2rem;
  font-family: "Open Sans Condensed", -apple-system, BlinkMacSystemFont, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: black;
  text-shadow: -1px -1px 0 #b80000, 1px -1px 0 #b80000, -1px 1px 0 #b80000, 1px 1px 0 #b80000;
}

.tickets a:hover {
  text-decoration: underline;
}

@-webkit-keyframes flickerAnimation {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes flickerAnimation {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.animate-flicker {
  -webkit-animation: flickerAnimation 2s infinite;
  animation: flickerAnimation 2s infinite;
}

@media all and (max-width: 800px) {
  .shows {
    grid-template-columns: 1fr 1fr;
  }
}

@media all and (max-width: 550px) {
  .shows {
    grid-template-columns: 1fr;
  }
}
